<script>
  import { onMount } from "svelte";
  import { fly } from "svelte/transition";

  import Tailwind from "./Tailwind.svelte";
  import PDFPage from "./PDFPage.svelte";
  import Image from "./Image.svelte";
  import Text from "./Text.svelte";
  import Drawing from "./Drawing.svelte";
  import DrawingCanvas from "./DrawingCanvas.svelte";

  import prepareAssets, { fetchFont } from "../utils/prepareAssets.js";
  import {
    readAsImage,
    readAsPDF,
    readAsDataURL,
  } from "../utils/asyncReader.js";
  import { ggID } from "../utils/helper.js";
  import { save } from "../utils/PDF.js";

  const genID = ggID();
  let flag = false;
  let pdfFile;
  let pdfName = "";
  let pages = [];
  let pagesScale = [];
  let allObjects = [];
  let currentFont = "Times-Roman";
  let selectedPageIndex = -1;
  let saving = false;
  let addingDrawing = false;

  window.onbeforeunload = () => {
    if (flag) return "";
  };

  onMount(async () => {
    try {
      const res = await fetch("test.pdf");
      const pdfBlob = await res.blob();
      await addPDF(pdfBlob);
      selectedPageIndex = 0;
      setTimeout(() => {
        fetchFont(currentFont);
        prepareAssets();
      }, 5000);
    } catch (e) {
      console.log(e);
    }
  });

  async function onUploadPDF(e) {
    const files = e.target.files || (e.dataTransfer && e.dataTransfer.files);
    const file = files[0];
    if (!file || file.type !== "application/pdf") return;
    selectedPageIndex = -1;
    try {
      await addPDF(file);
      flag = true;
      console.log("Upload PDF Fired!");
      selectedPageIndex = 0;
    } catch (e) {
      console.log(e);
    }
  }

  async function addPDF(file) {
    try {
      const pdf = await readAsPDF(file);
      pdfName = file.name;
      pdfFile = file;
      const numPages = pdf.numPages;
      pages = Array(numPages)
        .fill()
        .map((_, i) => pdf.getPage(i + 1));
      allObjects = pages.map(() => []);
      pagesScale = Array(numPages).fill(1);
    } catch (e) {
      console.log("Failed to add pdf.");
      throw e;
    }
  }

  async function onUploadImage(e) {
    const file = e.target.files[0];
    if (file && selectedPageIndex >= 0) {
      addImage(file);
      flag = true;
    }
    e.target.value = null;
  }

  async function addImage(file) {
    try {
      // get dataURL to prevent canvas from tainted
      const url = await readAsDataURL(file);
      const img = await readAsImage(url);
      const id = genID();
      const { width, height } = img;
      const object = {
        id,
        type: "image",
        width,
        height,
        x: 0,
        y: 0,
        payload: img,
        file,
      };
      allObjects = allObjects.map((objects, pIndex) =>
        pIndex === selectedPageIndex ? [...objects, object] : objects
      );
    } catch (e) {
      console.log(`Fail to add image.`, e);
    }
  }

  function onAddTextField() {
    if (selectedPageIndex >= 0) {
      addTextField();
    }
  }

  function addTextField(text = "New Text Field") {
    const id = genID();
    fetchFont(currentFont);
    const object = {
      id,
      text,
      type: "text",
      size: 16,
      width: 0, // recalculate after editing
      lineHeight: 1.4,
      fontFamily: currentFont,
      x: 0,
      y: 0,
    };

    allObjects = allObjects.map((objects, pIndex) =>
      pIndex === selectedPageIndex ? [...objects, object] : objects
    );

    console.log("Add text field fired!");
    flag = true;
  }

  function onAddDrawing() {
    if (selectedPageIndex >= 0) {
      addingDrawing = true;
      flag = true;
    }
  }

  function addDrawing(originWidth, originHeight, path, scale = 1) {
    const id = genID();
    const object = {
      id,
      path,
      type: "drawing",
      x: 0,
      y: 0,
      originWidth,
      originHeight,
      width: originWidth * scale,
      scale,
    };
    allObjects = allObjects.map((objects, pIndex) =>
      pIndex === selectedPageIndex ? [...objects, object] : objects
    );
  }

  function selectFontFamily(event) {
    const name = event.detail.name;
    fetchFont(name);
    currentFont = name;
  }

  function selectPage(index) {
    selectedPageIndex = index;
  }

  function updateObject(objectId, payload) {
    allObjects = allObjects.map((objects, pIndex) =>
      pIndex == selectedPageIndex
        ? objects.map((object) =>
            object.id === objectId ? { ...object, ...payload } : object
          )
        : objects
    );
  }

  function deleteObject(objectId) {
    allObjects = allObjects.map((objects, pIndex) =>
      pIndex == selectedPageIndex
        ? objects.filter((object) => object.id !== objectId)
        : objects
    );
  }

  function onMeasure(scale, i) {
    pagesScale[i] = scale;
  }

  // FIXME: Should wait all objects finish their async work
  async function savePDF() {
    if (!pdfFile || saving || !pages.length) return;
    saving = true;
    try {
      await save(pdfFile, allObjects, pdfName, pagesScale);
    } catch (e) {
      console.log(e);
    } finally {
      saving = false;
    }
  }

  function openDrawer(e) {
    const nav = document.querySelector(".toolbar");
    const hide = document.querySelector(".hide");
    const tools = document.querySelector(".tools");
    if (nav.classList.contains("toolbar--expand")) {
      nav.classList.remove("toolbar--expand");
      hide.classList.add("hidden");
      tools.classList.remove("justify-around");
      tools.classList.add("justify-center");
    } else {
      nav.classList.add("toolbar--expand");
      hide.classList.remove("hidden");
      tools.classList.add("justify-around");
      tools.classList.remove("justify-center");
      addTextField();
    }
  }
</script>

<svelte:window
  on:dragenter|preventDefault
  on:dragover|preventDefault
  on:drop|preventDefault={onUploadPDF}
/>
<Tailwind />
<main class="App flex flex-col items-center min-h-screen">
  <div
    class="header flex flex-row justify-around content-center items-center top-0 left-0 right-0 w-full"
  >
    <div
      class="flex flex-row justify-around lg:justify-between content-center items-center w-full"
    >
      <a href="https://a1office.co/">
        <img
          src="./A1-logo.svg"
          alt="Logo"
          class="lg:w-10 lg:h-10 w-8 h-8 lg:ml-20"
        />
      </a>
      <div class="flex justify-center items-center lg:ml-32">
        <input
          placeholder="Untitled Document"
          type="text"
          class="edit-pdf-name select-none bg-transparent"
          bind:value={pdfName}
        />
        <img src="./edit-icon.svg" alt="edit" class="lg:w-5 w-4" />
      </div>
      <div
        class="cursor-pointer flex justify-center items-center"
        on:click={savePDF}
      >
        <p
          class="hidden button download cursor-pointer flex justify-center items-center text-white lg:flex lg:mr-10"
        >
          Download
        </p>
        <img
          src="./download-icon.svg"
          alt="download"
          class="lg:hidden w-5"
        />
      </div>
    </div>
  </div>
  <div
    class="toolbar fixed z-10 bottom-0 left-0 right-0 flex justify-around items-center bg-white"
  >
    <input
      type="file"
      id="image"
      name="image"
      class="hidden"
      on:change={onUploadImage}
    />
    <input
      type="file"
      name="pdf"
      id="pdf"
      on:change={onUploadPDF}
      class="hidden"
    />
    <div
      class="tools flex flex-col justify-center items-center h-full w-screen"
    >
      <hr class="line-break" />
      <div class="flex flex-row w-full justify-evenly items-center mt-10">
        <div>
          <label
            class="flex items-center justify-center h-fit w-8 cursor-pointer"
            class:cursor-not-allowed={selectedPageIndex < 0}
            class:bg-gray-500={selectedPageIndex < 0}
            for="image"
          >
            <img
              src="./image-icon.svg"
              alt="Custom"
              class="lg:w-8"
            />
          </label>
          <p class="cursor-pointer">Image</p>
        </div>
        <div on:click={onAddDrawing}>
          <p
            class="flex items-center justify-center h-full w-8 cursor-pointer"
            class:cursor-not-allowed={selectedPageIndex < 0}
            class:bg-gray-500={selectedPageIndex < 0}
          >
            <img src="./draw-icon.svg" alt="draw" class="lg:w-8" />
          </p>
          <p class="cursor-pointer">Draw</p>
        </div>
        <div on:click={openDrawer}>
          <label
            class="flex items-center justify-center h-full w-8 
            cursor-pointer"
            for="text"
            class:cursor-not-allowed={selectedPageIndex < 0}
            class:bg-gray-500={selectedPageIndex < 0}
          >
            <img
              src="./add-text-icon.svg"
              alt="add text"
              class="lg:w-8"
            />
          </label>
          <p class="cursor-pointer">Text</p>
        </div>
        <div>
          <label
            class="choose-file button cursor-pointer flex justify-center items-center whitespace-no-wrap text-white
            px-3 rounded w-24 h-8 mb-4 lg:rounded-xl lg:w-32 lg:h-12 md:rounded-lg md:w-32 md:h-10"
            for="pdf"
          >
            Choose PDF
          </label>
        </div>
      </div>
      <div class="hide hidden w-full justify-center items-center flex flex-col">
        <hr class="line-break-2 w-full" />
        <div class="h-12 mb-4" />
      </div>
    </div>
  </div>
  {#if addingDrawing}
    <div
      transition:fly={{ y: -200, duration: 500 }}
      class="hover-pencil fixed z-10 top-0 left-0 right-0 bg-white
      shadow-lg"
      style="height: 50%;"
    >
      <DrawingCanvas
        on:finish={(e) => {
          const { originWidth, originHeight, path } = e.detail;
          let scale = 1;
          if (originWidth > 500) {
            scale = 500 / originWidth;
          }
          addDrawing(originWidth, originHeight, path, scale);
          addingDrawing = false;
        }}
        on:cancel={() => (addingDrawing = false)}
      />
    </div>
  {/if}
  {#if pages.length}
    <div class="w-full">
      {#each pages as page, pIndex (page)}
        <div
          class="p-5 w-full flex flex-col items-center overflow-hidden"
          on:mousedown={() => selectPage(pIndex)}
          on:touchstart={() => selectPage(pIndex)}
        >
          <div class="canvas relative">
            <PDFPage
              on:measure={(e) => onMeasure(e.detail.scale, pIndex)}
              {page}
            />
            <div
              class="absolute top-0 left-0 transform origin-top-left"
              style="transform: scale({pagesScale[
                pIndex
              ]}); touch-action: none;"
            >
              {#each allObjects[pIndex] as object (object.id)}
                {#if object.type === "image"}
                  <Image
                    on:update={(e) => updateObject(object.id, e.detail)}
                    on:delete={() => deleteObject(object.id)}
                    file={object.file}
                    payload={object.payload}
                    x={object.x}
                    y={object.y}
                    width={object.width}
                    height={object.height}
                    pageScale={pagesScale[pIndex]}
                  />
                {:else if object.type === "text"}
                  <Text
                    on:update={(e) => updateObject(object.id, e.detail)}
                    on:delete={() => deleteObject(object.id)}
                    on:selectFont={selectFontFamily}
                    text={object.text}
                    x={object.x}
                    y={object.y}
                    size={object.size}
                    lineHeight={object.lineHeight}
                    fontFamily={object.fontFamily}
                    pageScale={pagesScale[pIndex]}
                  />
                {:else if object.type === "drawing"}
                  <Drawing
                    on:update={(e) => updateObject(object.id, e.detail)}
                    on:delete={() => deleteObject(object.id)}
                    path={object.path}
                    x={object.x}
                    y={object.y}
                    width={object.width}
                    originWidth={object.originWidth}
                    originHeight={object.originHeight}
                    pageScale={pagesScale[pIndex]}
                  />
                {/if}
              {/each}
            </div>
          </div>
        </div>
      {/each}
    </div>
  {:else}
    <div class="w-full flex-grow flex justify-center items-center">
      <span class=" font-bold text-3xl text-gray-500">Drag something here</span>
    </div>
  {/if}
</main>
